import type { ComponentType, ReactNode } from 'react';
import classNames from 'classnames';
import styles from './text.module.scss';

export type TextProps = {
  children?: ReactNode;
  element?: 'div' | 'p' | 'span' | 'none';
  className?: string;
} & React.HTMLAttributes<HTMLElement>;

export function Text({ children, className, element = 'span', style, ...rest }: TextProps) {
  // @ts-ignore
  const Element: ComponentType<React.HTMLAttributes<HTMLElement>> = element;
  if (element === 'none') return <>{children}</>;

  return (
    <Element {...rest} className={classNames(className, styles.text)}>
      {children}
    </Element>
  );
}
